<template>
	<header id="panda_header">
		<div class="pheader">
			<div class="pheader-inner">
				<div class="ph-logo">
					<a href="javascript:void(0)">
						<img src="../img/header/b2a97149ec43dfc95eb177508af29f6c.web.png">
					</a>
				</div>
				<div class="ph-nav">
					<div class="ph-nav_shadow" style="left: 0px; width: 62px;">
					</div>
					<ul class="ph-nav_first">
						<li class="ph-nav_item" v-for="item in nav_first">
							<a href="javascript:void(0);">{{item}}</a>
						</li>
					</ul>
				</div>
				<div class="ph-myinfo">
					<div class="ph-myinfo-block pheader--effect-simo">
						<img class="ph-myinfo-block_photo" src="../img/header/0257040380bd7d294a8017532c07e39b.jpg" />
					</div>
				</div>
				<div class="ph-follow">
					<div class="ph-follow-block">
						<span class="ph-follow_icon ricon ricon-star"></span>
					</div>
				</div>
				<div class="ph-alive">
					<a href="javascript:void(0)" title="开播"><span class="ph-alive_icon ricon ricon-live"></span></a>
				</div>
				<div class="ph-download">
					<a href="javascript:void(0)" title="下载"><span class="ph-download_icon ricon ricon-download"></span></a>
				</div>
				<div class="ph-search">
					<form action="" name="room-search" method="get" target="_top">
						<input type="text" name="kw" id="ph-searchInput" class="ph-search_input" placeholder="搜游戏/主播" />
						<a href="javascript:void(0)" class="ph-search_submit ricon ricon-search"></a>
					</form>
				</div>
			</div>
		</div>
	</header>
</template>

<script>
	export default {
		data: function() {
			return {
				nav_first: ['首页', '全部', '分类', '熊猫精品', 'iPanda', '熊猫游戏']
			}
		}
	}
</script>

<style>

</style>


